import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import { Navbar, Nav } from "react-bootstrap" 

const Header = ({ siteTitle }) => (
  <header>
    <Navbar className="fab-navbar">
      <div className="container">
        <Navbar.Brand href="/">{siteTitle}</Navbar.Brand>
        <Nav className="mr-auto">
          <Link className="nav-link" disabled to="/courses/">Courses</Link>
          <Link className="nav-link" to="/wiki/">Wiki</Link>
          <Link className="nav-link" to="/workshops/">Workshops</Link>
          <Link className="nav-link" to="/files/">Files</Link>
        </Nav>
      </div>
    </Navbar>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
